* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wallet-page {
  align-items: center;
  background-image: url('./imgs/foto-fundo.png');
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.bg-white {
  background: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: -4px 9px 13px 0 rgba(5, 18, 54, 0.4);
  flex-shrink: 0;
  height: 387px;
  width: 1037px;
  z-index: 1;
}
